@import url(https://fonts.googleapis.com/css2?family=PT+Mono&family=Source+Sans+Pro:wght@200;400;700&family=Tillana:wght@400;600&display=swap);
#controls{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}button{text-align:center}
#inputter{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:10px;gap:10px}label{font-family:"Tillana",cursive;font-size:1.5rem;font-weight:bold;text-align:center;color:purple}#editor{font-family:"PT Mono",monospace;padding:.5rem;-webkit-flex-grow:1;flex-grow:1}
#outputter{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:10px;gap:10px}h3{font-family:"Tillana",cursive;font-size:1.5rem;text-align:center;color:orange}#preview{background-color:#d3d3d3;-webkit-flex-grow:1;flex-grow:1;padding:.5rem;overflow:scroll}#preview h1,#preview h2,#preview h3,#preview p,#preview code,#preview pre,#preview blockquote,#preview table,#preview ul,#preview ol,#preview table{font-family:"Source Sans Pro",sans-serif;margin-bottom:.5em;color:inherit;text-align:left}#preview h1{font-size:2.5rem}#preview h2{font-size:2rem}#preview h3{font-size:1.75rem}#preview pre,#preview blockquote{border-top:1px solid gray;padding:.5rem;border-bottom:1px solid gray}#preview blockquote{font-style:italic}#preview code{font-family:"PT Mono",monospace}#preview table{border-spacing:.5em .25em;padding:.5em;border:1px solid gray}#preview table>thead>tr>th{border-bottom:1px #000 solid}
#react-app{height:calc(100vh - 20px);margin:10px auto;display:grid}#react-app>div:first-child{grid-area:control}#react-app>div:nth-child(2){grid-area:inputter}#react-app>div:nth-child(3){grid-area:outputter}#react-app.fullView{margin:10px 1%;height:calc(100vh - 20px);grid-gap:10px 20px;gap:10px 20px;grid-template-columns:repeat(2, minmax(0, 1fr));grid-template-rows:-webkit-max-content minmax(0, 1fr);grid-template-rows:max-content minmax(0, 1fr);grid-template-areas:"control control" "inputter outputter";-webkit-justify-content:center;justify-content:center}#react-app.mobileView{height:-webkit-max-content;height:max-content;margin:10px;grid-gap:10px;gap:10px;grid-template-columns:minmax(0, 1fr);grid-template-rows:-webkit-max-content minmax(-webkit-max-content, 80vh);grid-template-rows:max-content minmax(max-content, 80vh);grid-template-areas:"control" "inputter" "outputter"}
*{margin:0}body{margin:0;font-family:"Source Sans Pro",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:"PT Mono",monospace}
